.chatPage{
background-color: rgb(31, 29, 29);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.chatContainer{
    background-color: white;
    height:60%;
    width: 50%;
}
.header{
    background-color: crimson;
    height: 15%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header> h2{
      color: white;
      margin:1.5vmax;
}
.header>a> img{
    margin:1.5vmax;
    height: 30px;
    transition: all 0.3s;
    cursor: pointer;

}
.header> img:hover{
    transform: scale(1.2);
}
.chatBox{

height:70%;
box-sizing: border-box;

}
.inputBox{
   
    height:15%;
    box-sizing: border-box;
    display: flex;
}
#chatInput{
    width:85%;
    padding: 1vmax;
     border: none;
    border-color: white;
    outline: none;
    font-size: 1.2vmax;
    border-top: 0.2vmax solid black;
}
.sendBtn{
    background-color: crimson;
    color:white;
    border:none;
    width:20%;
    transition: all 0.3s;
    cursor: pointer;
}
.sendBtn:hover{
    background-color: rgb(112, 11, 31);
}