@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap");
.JoinPage {
  background-color: rgb(31, 29, 29);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.JoinContainer {
  
  height: 60%;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.JoinContainer > img {
  width: 6vmax;
}
.JoinContainer > h1 {
  color: white;
  font-size: 2vmax;
  width: 25vmax;
  border-bottom: 0.2vmax solid white;
  text-align: center;
  padding: 20px;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}
#joinInput {
  width: 25vmax;
  padding: 1.5vmax;
  border: none;
  outline: none;
  font-size: 1.2vmax;
  margin: 1vmax;
  box-sizing: border-box;
}
.btn {
  width: 25vmax;
  padding: 1.5vmax;
  border: none;
  outline: none;
  color: white;

  font-family: "Roboto", sans-serif;
  font-size: 1.2vmax;
  background-color: crimson;
  transition: all 0.3s;
  cursor: pointer;
}
.btn:hover {
  background-color: rgb(152, 25, 50);
}
